import React from 'react'
import HomeIcon from '@mui/icons-material/Home';
import { Link } from 'react-router-dom';
import './App.css'
const Card = () => {
  return (
    <div>
      <nav class="nav"><Link to='/' className='home'><HomeIcon/></Link></nav>
    </div>
  )
}

export default (Card)