import Api from './Api'
import {Link} from 'react-router-dom'
import './App.css'
import HomeIcon from '@mui/icons-material/Home';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
const Home = () => {
  return (
    <div>
        <nav class="nav"><div className='home'><HomeIcon/>Home</div>
     <Link to='/Card' className="link"><ShoppingCartIcon/></Link></nav>
        <Api/>
    </div>
  )
}

export default Home